oh-table,
oh-table-title-footer,
oh-table-inner-scroll,
oh-table-inner-default,
oh-table-selection {
  display: block;
}

.oh-table-out-bordered {
  > .@{table-prefix-cls}-title {
    border: @table-border;
    border-bottom: 0;
  }
  > .@{table-prefix-cls}-container {
    border: @table-border;
    border-bottom: 0;
  }
  > .@{table-prefix-cls}-footer {
    border: @table-border;
    border-top: 0;
  }
}

cdk-virtual-scroll-viewport.ui-table-body {
  overflow-y: scroll;
}

.oh-table-hide-scrollbar {
  scrollbar-color: @table-header-bg @table-header-bg;
  &::-webkit-scrollbar {
    background-color: @table-header-bg;
  }
}

.@{table-prefix-cls}.@{table-prefix-cls}-small {
  .oh-table-hide-scrollbar {
    scrollbar-color: @table-header-bg-sm @table-header-bg-sm;
    &::-webkit-scrollbar {
      background-color: transparent;
    }
  }
}

.ui-table-wrapper-rtl .ui-table thead > tr > th.ui-table-selection-column {
  text-align: center;
}
