@import '../../style/themes/default';
@import '../../style/mixins/index';

@result-prefix-cls: ~'@{ui-prefix}-result';

.@{result-prefix-cls} {
  padding: 48px 32px;
  text-align: center;
  // status color
  &-success &-icon > .ms-Icon {
    color: @success-color;
  }

  &-error &-icon > .ms-Icon {
    color: @error-color;
  }

  &-info &-icon > .ms-Icon {
    color: @info-color;
  }

  &-warning &-icon > .ms-Icon {
    color: @warning-color;
  }

  // Exception Status image
  &-image {
    width: 250px;
    height: 295px;
    margin: auto;
  }

  &-icon {

    > .ms-Icon {
      font-size: @result-icon-font-size;
    }
  }
  &-icon-exception{
    float: right;
    margin-right: 60px;

    svg{
      max-width: 180px;
    }

    @media screen and (max-width: @screen-md-max) {
      //do Smth
      float: none;
      margin-right: 0px;
    }

  }
  &-content-exception{
    text-align-last: start;
    padding: 0px 60px;
    & .@{result-prefix-cls}-subtitle {
    text-align: initial;
    }
    @media screen and (max-width: @screen-md-max) {
      //do Smth
      text-align-last: center;
    }
    // float: left;
  }
  &-title {
    color: @heading-color;
    font-size: @result-title-font-size;
    line-height: 1.8;
    text-align: center;
  }
  &-title-exception {
    color: @heading-color;
    line-height: 1.8;
    text-align: center;
    font-size: @result-title-exception-font-size;
  }

  &-maintitle {
    color: @result-maintitle-color;
    font-size: @result-maintitle-font-size;
    line-height: 1.6;
    text-align: initial;
  }

  &-subtitle {
    color: @text-color-secondary;
    font-size: @result-subtitle-font-size;
    line-height: 1.6;
    text-align: center;
  }

  &-extra {
    margin: @result-extra-margin;
    text-align: center;
    > * {
      margin-right: 8px;

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &-content {
    margin-top: 24px;
    padding: 24px 40px;
    background-color: @background-color-light;
  }
}

@import './rtl';
