oh-tree-virtual-scroll-view {
  display: block;
  position: relative;
  overflow: auto;
  contain: strict;
  transform: translateZ(0);
  will-change: scroll-position;
  -webkit-overflow-scrolling: touch;
  .ui-tree-list, .ui-tree-list-holder {
    height: 100%;
  }
}

oh-tree-virtual-scroll-view, oh-tree-view {
  .ui-tree-switcher + .ui-tree-switcher.oh-tree-leaf-line-icon {
    display: none;
  }
}

oh-tree-view .ui-tree-list-holder-inner {
  display: flex;
  flex-direction: column;
}
