@import '../../style/themes/index';
@import '../../style/mixins/index';
@logo-prefix: ~'@{ui-prefix}-oh-logo';

.@{logo-prefix}-svg{
  display: inline-block!important;
}
.@{logo-prefix}-ohSize{
  &-xs{
    font-size: @logo-xs;
    position: relative;
    .@{logo-prefix}-title {
      font-size: 10px;
      line-height: 18px;
      padding-left: 5px;
      margin-left: 3px;
      vertical-align: text-top;
    }
    .@{logo-prefix}-divider {
      height: 12px;
      top: 3px;
      margin-left: 2px;
      width: 1px;
       display: inline-block;
      position: absolute;
      opacity: 0.6;
    }
  }
  &-sm{
    font-size: @logo-sm;
    position: relative;
    .@{logo-prefix}-title {
      font-size: 12px;
      line-height: 22px;
      padding-left: 5px;
      margin-left: 3px;
      vertical-align: text-top;

    }
    .@{logo-prefix}-divider {
      height: 14px;
      top: 4px;
      width: 1px;
       display: inline-block;
      position: absolute;
      margin-left: 2px;
      opacity: 0.6;
    }
  }
  &-md{
    font-size: @logo-md;
    position: relative;
    .@{logo-prefix}-title {
      font-size: 16px;
      line-height: 30px;
      padding-left: 5px;
      margin-left: 3px;
      vertical-align: text-top;

    }
    .@{logo-prefix}-divider {
      height: 18px;
      top: 7px;
      margin-left: 2px;
      width: 1px;
      display: inline-block;
      position: absolute;
      opacity: 0.6;
    }
  }
  &-lg{
    font-size: @logo-lg;
    position: relative;
   .@{logo-prefix}-title {
     font-size: 20px;
     line-height: 34px;
     padding-left: 10px;
     margin-left: 5px;
     vertical-align: text-top;
   }
   .@{logo-prefix}-divider {
     height: 20px;
     top: 8px;
     margin-left: 4px;

     width: 1px;
     display: inline-block;
     position: absolute;
     opacity: 0.6;
   }
  }
  &-xl {
    font-size: @logo-xl;
    position: relative;
    .@{logo-prefix}-title {
      font-size: 22px;
      line-height: 38px;
      padding-left: 10px;
      margin-left: 5px;
      vertical-align: text-top;
    }
    .@{logo-prefix}-divider {
      height: 24px;
      top: 8px;
      margin-left: 4px;
      width: 1px;
      display: inline-block;
      position: absolute;
      opacity: 0.6;
    }
  }
  &-xxl{
    font-size: @logo-xxl;
    position: relative;
    .@{logo-prefix}-title {
      font-size: 24px;
      line-height: 42px;
      padding-left: 10px;
      margin-left: 5px;
      vertical-align: text-top;
    }
    .@{logo-prefix}-divider {
      height: 23px;
      margin-left: 4px;
      top:10px;
      width: 1px;
      display: inline-block;
      position: absolute;
      opacity: 0.6;
    }

  }
  &-liteMode{

    .@{logo-prefix}-svg-text{
      display: none;
    }
  }
  &-customSvg{
    .@{logo-prefix}-svg{
      display: inline-block;
      width: 50px;
    }
  }
  &-img-Logo-sm{
    height: 20px;
    vertical-align: middle;
    object-fit: cover;
  }
  &-img-Logo-md{
    height: 28px;
    vertical-align: middle;
    object-fit: cover;
  }
  &-img-Logo-lg{
    height: 32px;
    vertical-align: middle;
    object-fit: cover;
  }

  &-img-Logo-xl{
    height: 36px;
    vertical-align: middle;
    object-fit: cover;
  }
  &-img-Logo-xxl{
    height: 40px;
    vertical-align: middle;
    object-fit: cover;
  }

}
