@import '../../style/themes/index';
@import '../../style/mixins/index';

@input-affix-with-clear-btn-width: 38px;

// size mixins for input
.input-lg() {
  padding: 0px @input-padding-horizontal-lg;
  font-size: @font-size-lg;
  height: @height-lg;
}

.input-sm() {
  padding: @input-padding-vertical-sm @input-padding-horizontal-sm;
  height: @height-sm;
}

// input status
// == when focus or actived
.active(@color: @outline-color) {
  & when (@theme =dark) {
    border-color: @color;
  }

  & when not (@theme =dark) {
    border-color:~`colorPalette('@{color}', 5) `;
  }

  border-right-width: @border-width-base  !important;
  outline: 0;
  // box-shadow: @input-outline-offset @outline-blur-size @outline-width fade(@color, @outline-fade);
}

// == when hoverd
.hover(@color: @input-hover-border-color) {
  border-color: @color;
  border-right-width: @border-width-base  !important;
}

.disabled() {
  color: @input-disabled-color;
  background-color: @input-disabled-bg;
  cursor: not-allowed;
  opacity: 1;

  &:hover {
    .hover(@input-border-color);
  }
}

// Basic style for input
.input() {
  position: relative;
  display: inline-block;
  width: 100%;
  min-width: 0;
  padding: @input-padding-vertical-base @input-padding-horizontal-base;
  color: @input-color;
  font-size: @font-size-base;
  line-height: @line-height-base;
  background-color: @input-bg;
  background-image: none;
  border: @border-width-base @border-style-base @input-border-color;
  border-radius: @border-radius-base;
  transition: all 0.3s;
  .placeholder(); // Reset placeholder

  &:hover {
    .hover();
  }

  &::placeholder {
    color: @input-border-color;
  }

  &:hover::placeholder {
    color: fade(@input-hover-border-color, 85%);
  }


  &:focus,
  &-focused {
    .active();

    &::placeholder {
      color: @input-hover-border-color;
    }
  }

  &-disabled {
    .disabled();
  }

  &[disabled] {
    .disabled();

    &:hover::placeholder {
      color: @input-border-color;
    }
  }

  &-borderless {

    &,
    &:hover,
    &:focus,
    &-focused,
    &-disabled,
    &[disabled] {
      background-color: transparent;
      border: none;
      box-shadow: none;
    }
  }

  // Reset height for `textarea`s
  textarea& {
    max-width: 100%; // prevent textearea resize from coming out of its container
    height: auto;
    min-height: @input-height-base;
    line-height: @line-height-base;
    vertical-align: bottom;
    transition: all 0.3s, height 0s;
  }

  // Size
  &-lg {
    .input-lg();
  }

  &-sm {
    .input-sm();
  }
}

// label input
.input-group(@inputClass) {
  position: relative;
  display: table;
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;

  // Undo padding and float of grid classes
  &[class*='col-'] {
    float: none;
    padding-right: 0;
    padding-left: 0;
  }

  >[class*='col-'] {
    padding-right: 8px;

    &:last-child {
      padding-right: 0;
    }
  }

  &-addon,
  &-wrap,
  >.@{inputClass} {
    display: table-cell;

    &:not(:first-child):not(:last-child) {
      border-radius: 0;
    }
  }

  &-addon,
  &-wrap {
    width: 1px; // To make addon/wrap as small as possible
    white-space: nowrap;
    vertical-align: middle;
  }

  &-wrap>* {
    display: block !important;
  }

  .@{inputClass} {
    float: left;
    width: 100%;
    margin-bottom: 0;
    text-align: inherit;

    &:focus {
      z-index: 1; // Fix https://storage-stagging.vnpt.vn/oneui/docs/imgs/DHNpoqfMXSfrSnlZvhsJ.png
      border-right-width: 1px;
    }

    &:hover {
      z-index: 1;
      border-right-width: 1px;

      .@{ui-prefix}-input-search-with-button & {
        z-index: 0;
      }
    }
  }

  &-addon {
    position: relative;
    padding: 0 @input-padding-horizontal-base;
    color: @input-color;
    font-weight: normal;
    font-size: @font-size-base;
    text-align: center;
    background-color: @input-addon-bg;
    border: @border-width-base @border-style-base @input-border-color;
    border-radius: @border-radius-base;
    transition: all 0.3s;

    // Reset Select's style in addon
    .@{ui-prefix}-select {
      margin: -(@input-padding-vertical-base + 1px) (-@input-padding-horizontal-base);

      &.@{ui-prefix}-select-single:not(.@{ui-prefix}-select-customize-input) .@{ui-prefix}-select-selector {
        background-color: inherit;
        border: @border-width-base @border-style-base transparent;
        box-shadow: none;
      }

      &-open,
      &-focused {
        .@{ui-prefix}-select-selector {
          color: @primary-color;
        }
      }
    }
  }

  // Reset rounded corners
  >.@{inputClass}:first-child,
  &-addon:first-child {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;

    // Reset Select's style in addon
    .@{ui-prefix}-select .@{ui-prefix}-select-selector {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  >.@{inputClass}-affix-wrapper {
    &:not(:first-child) .@{inputClass} {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }

    &:not(:last-child) .@{inputClass} {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  &-addon:first-child {
    border-right: 0;
  }

  &-addon:last-child {
    border-left: 0;
  }

  >.@{inputClass}:last-child,
  &-addon:last-child {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;

    // Reset Select's style in addon
    .@{ui-prefix}-select .@{ui-prefix}-select-selector {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }

  // Sizing options
  &-lg .@{inputClass},
  &-lg>&-addon {
    .input-lg();
  }

  &-sm .@{inputClass},
  &-sm>&-addon {
    .input-sm();
  }

  // Fix https://github.com/ui-design/ui-design/issues/5754
  &-lg .@{ui-prefix}-select-single .@{ui-prefix}-select-selector {
    height: @input-height-lg;
  }

  &-sm .@{ui-prefix}-select-single .@{ui-prefix}-select-selector {
    height: @input-height-sm;
  }

  .@{inputClass}-affix-wrapper {

    &-lg {
      padding: 0px @input-padding-horizontal-lg;
      height: @height-lg + 2px;
    }

    &:not(:first-child) {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }

    &:not(:last-child) {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;

      .@{ui-prefix}-input-search & {
        border-top-left-radius: @border-radius-base;
        border-bottom-left-radius: @border-radius-base;
      }
    }
  }

  &&-compact {
    display: block;
    .clearfix();

    &-addon,
    &-wrap,
    >.@{inputClass} {
      &:not(:first-child):not(:last-child) {
        border-right-width: @border-width-base;

        &:hover {
          z-index: 1;
        }

        &:focus {
          z-index: 1;
        }
      }
    }

    &>* {
      display: inline-block;
      float: none;
      vertical-align: top; // https://github.com/ui-design/ui-design-pro/issues/139
      border-radius: 0;
    }

    &>.@{inputClass}-affix-wrapper {
      display: inline-flex;
    }

    &>.@{ui-prefix}-picker-range {
      display: inline-flex;
    }

    &>*:not(:last-child) {
      margin-right: -@border-width-base;
      border-right-width: @border-width-base;
    }

    // Undo float for .ui-input-group .ui-input
    .@{inputClass} {
      float: none;
    }

    // reset border for Select, DatePicker, AutoComplete, Cascader, Mention, TimePicker, Input
    &>.@{ui-prefix}-select>.@{ui-prefix}-select-selector,
    &>.@{ui-prefix}-select-auto-complete .@{ui-prefix}-input,
    &>.@{ui-prefix}-cascader-picker .@{ui-prefix}-input,
    &>.@{ui-prefix}-input-group-wrapper .@{ui-prefix}-input {
      border-right-width: @border-width-base;
      border-radius: 0;

      &:hover {
        z-index: 1;
      }

      &:focus {
        z-index: 1;
      }
    }

    &>.@{ui-prefix}-select-focused {
      z-index: 1;
    }

    // update z-index for arrow icon
    &>.@{ui-prefix}-select>.@{ui-prefix}-select-arrow {
      z-index: 1; // https://github.com/ui-design/ui-design/issues/20371
    }

    &>*:first-child,
    &>.@{ui-prefix}-select:first-child>.@{ui-prefix}-select-selector,
    &>.@{ui-prefix}-select-auto-complete:first-child .@{ui-prefix}-input,
    &>.@{ui-prefix}-cascader-picker:first-child .@{ui-prefix}-input {
      border-top-left-radius: @border-radius-base;
      border-bottom-left-radius: @border-radius-base;
    }

    &>*:last-child,
    &>.@{ui-prefix}-select:last-child>.@{ui-prefix}-select-selector,
    &>.@{ui-prefix}-cascader-picker:last-child .@{ui-prefix}-input,
    &>.@{ui-prefix}-cascader-picker-focused:last-child .@{ui-prefix}-input {
      border-right-width: @border-width-base;
      border-top-right-radius: @border-radius-base;
      border-bottom-right-radius: @border-radius-base;
    }

    // https://github.com/ui-design/ui-design/issues/12493
    &>.@{ui-prefix}-select-auto-complete .@{ui-prefix}-input {
      vertical-align: top;
    }

    .@{ui-prefix}-input-group-wrapper+.@{ui-prefix}-input-group-wrapper {
      margin-left: -1px;

      .@{ui-prefix}-input-affix-wrapper {
        border-radius: 0;
      }
    }

    .@{ui-prefix}-input-group-wrapper:not(:last-child) {
      &.@{ui-prefix}-input-search>.@{ui-prefix}-input-group {
        &>.@{ui-prefix}-input-group-addon>.@{ui-prefix}-input-search-button {
          border-radius: 0;
        }

        &>.@{ui-prefix}-input {
          border-radius: @border-radius-base 0 0 @border-radius-base;
        }
      }
    }
  }
}